@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;700&display=swap');

body {
  background-color: rgb(38, 32, 54);
}

.header-text {
  font-size: 2.75em;
  font-family: 'Mulish', sans-serif;
  font-weight: 700;
}

.footer-text {
  font-size: 0.75em;
  font-family: 'Mulish', sans-serif;
}

@media not all and (min-width: 640px) {
  .header-text {
      font-size: 2rem;
  }
}

:root {
  --fc-button-bg-color: #5f4c92;
  --fc-button-border-color: #5f4c92;
  --fc-button-hover-bg-color: #6d5ba0;
  --fc-button-hover-border-color: #6d5ba0;
  --fc-button-active-bg-color: #43336f;
  --fc-button-active-border-color: #151e27;

  --fc-border-color: rgb(255, 255, 255);

  --fc-neutral-text-color: white;
}